import { styled } from '@mui/material';
import { isSmallerThan } from '../../services/size-service';

/**
 * Highlight and/or color text with optional padding.
 */
const Colored = styled('span', {
  shouldForwardProp: (prop) =>
    prop !== 'bgColor' && prop !== 'padding' && prop !== 'paddingLeft' && prop !== 'paddingRight' && prop !== 'noise',
})<{ bgColor?: string; padding?: string; paddingLeft?: string; paddingRight?: string; noise?: boolean }>(
  ({ color, bgColor, padding, paddingLeft, paddingRight, noise = true, theme }) => ({
    color,
    backgroundColor: bgColor,
    backgroundImage: noise
      ? // https://stackoverflow.com/a/13996191/3076390
        'url(data:image/png;base64,)'
      : 'initial',
    padding,
    paddingLeft: !padding && paddingLeft === undefined && !isSmallerThan(theme, 'sm') ? 10 : paddingLeft,
    paddingRight: !padding && paddingRight === undefined && !isSmallerThan(theme, 'sm') ? 10 : paddingRight,
    display: 'inline-block',
  }),
);

export default Colored;
